<!--运动入口-->
<template>
    <!--头部-->
    <s-header :name="'运动追踪'" :rightName="'历史数据'" :rightPress="headRightclick"/>
    <!--日期选择-->
    <change-date2 :selectdate="selectdate" :onchange="onchange"/>
    <div class="div_content">
      <div class="div_question" @click="isShowDialog = true">如何解决“停止计步”问题？</div>
        <div class="div_top">
            <!--选择设备-->

          <change-device fromPage="motion" :otherAddClick="onAddRecord" fontColor="#06B48D" backgroundColor = "#fff"/>
          <div :style="{ width:'100%',marginLeft: '10px', marginRight: '10px'}" >
            <!--圆环显示-->
            <three-circle
              :dataleft="detial.maxStepNum"
              :datacenter="detial.heat"
              :dataright="detial.maxDistance"
              :desleft="'步数'"
              :descenter="'总消耗'"
              :unitcenter="'千卡'"
              :desright="'公里'"
              :mColor="'#F69654'"
            />
          </div>
            <div class="div_des">
                运动时长：<span>{{ detial.hour }}</span>小时<span>{{ detial.minute }}</span>分钟
            </div>

            <!--运动状态-->
            <track-type :datas="['运动不足','运动正常','运动充分','运动过量']" :type="detial.type + 1"/>
        </div>
        <track-data :title="'数据解读'">
          <template #inner >
            {{ detial.typeStr }}
          </template>
        </track-data>
        <track-data :title="'温馨提示'">
          <template #inner >
            <div v-if="detial.buTitleContentList[0].desc.length === 0">暂无数据</div>
            <div v-else-if="detial.buTitleContentList[0].desc.length !== 0" v-for="(item,index) in detial.buTitleContentList[0].desc" :key="index">
              <div>{{ item }}</div>
            </div>
          </template>
        </track-data>
    </div>

    <!--授权弹出框-->
    <van-overlay :show="isShowDialog" >
      <div class="wrapper" @click.stop>
        <div class="box_dialog">
          <div class="Icon_box_dialog" >
            <span @click="isShowDialog = false" >关闭</span>
          </div>
          <div class="block" >
            <div>需要授权</div>
            <div>确保计步器正常稳定使用</div>
            <van-image fit="cover" width="100%" height="140" :src="require('@/assets/bg_motion.jpg')" />
            <van-cell @click="onAutoStart" title="自动启动" icon="setting-o"  value="去设置" is-link></van-cell>
            <van-cell  @click="onBatteryControl"  title="调整电池优化" icon="setting-o"  value="去设置" is-link></van-cell>
          </div>
        </div>
      </div>
    </van-overlay>
</template>

<script>
  import sHeader from '@/components/SimpleHeader'
  import ChangeDate2 from '../components/ChangeDate2'
  import ThreeCircle from '../../../components/ThreeCircle'
  import {reactive, onMounted, toRefs,ref} from 'vue'
  import ChangeDevice from '../components/ChangeDevice'
  import TrackType from '../components/TrackType'
  import TrackData from '../TrackData'
  import {useRouter} from "vue-router";
  import { getDayDetial } from "@/service/motion";
  import {formatYYMMDD} from "@/utils/util";

  export default {
    name: "index",
    components: {
      sHeader,
      ChangeDate2,
      ChangeDevice,
      ThreeCircle,
      TrackType,
      TrackData,
    },
    setup() {
      const router = useRouter()

      onMounted(
        () =>{
            requestDayDetial()
        }
      )

      // 获取详情
      const requestDayDetial = async () => {
        const res = await getDayDetial( formatYYMMDD(state.selectdate), formatYYMMDD(state.selectdate))
        const {code,data} = res

        state.detial = code === 200 ? res.data : {}

      }


      const isShowDialog = ref(false);

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        todo: '',
        selectdate: new Date(),
        detial:{
          buTitleContentList:[{desc:[]}],
          typeStr:"暂无数据",
          type: 0,
          hour:'-',
          minute:'-',
          heat: 0,
          maxDistance: 0,
          maxStepNum: 0,
        },
      })

      // 调整电池优化
      const onBatteryControl = () => {

      }

      // 自动启动
      const onAutoStart = () => {

      }

      // 历史数据点击事件
      const headRightclick = () => {
        router.push("/history-momtion-record")
      }


      // 添加数据
      const onAddRecord = () => {
        router.push( {path:"/motion-add-record", query: { selectdate: state.selectdate } })
      }

      const onTypeClick = (index) => {
        alert(index)
      }
      const onchange = (data) => {
        state.selectdate = data
      }
      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        onTypeClick,
        onchange,
        onAddRecord,
        headRightclick,
        onAutoStart,
        isShowDialog,
        onBatteryControl
      }
    }
  }
</script>

