<!--选择日期的第二种样式-->

<template>
    <van-popup v-model:show="show_picker"  position="bottom">
        <van-datetime-picker
                v-model="currentDate"
                type="date"
                title="选择年月日"
                :min-date="minDate"
                :max-date="maxDate"
                @confirm="confirm"
                @cancel="cancel"
        />
    </van-popup>
    <div class="div_changedate2_content">
        <div class="div_changedate2_content_item" @click="left">
            <img  :src="require('@/assets/white_left_rrow.png')"  aria-hidden="true" class="svg_arraw" style="margin-right: 10px"/>
        </div>
        <div class="div_changedate2_content_item">
            <div style="display: flex;flex-direction: column;align-items: center">
                <span class="span_changedate2_des">{{ selectdate.toDateString() ===  new Date().toDateString() ? "今天" : "" }}</span>
                <span class="span_changedate2_date">{{ moment(selectdate).format('YYYY年MM月DD日')}}</span>
            </div>
        </div>
        <div class="div_changedate2_content_item">
            <div style="display: flex;align-items: center;justify-content: flex-end">
                <svg aria-hidden="true" class="svg_date" @click="onshow">
                    <use xlink:href="#icon-rili"></use>
                </svg>
                <img :src="require('@/assets/white_right_rrow.png')" aria-hidden="true" class="svg_arraw" :style="{ paddingLeft: '10px',}" @click="right"/>
            </div>
        </div>
    </div>
</template>
<script>
  import {reactive, onMounted,toRefs} from 'vue'
  import moment from 'moment'

  export default {
    name: "ChangeDate2",
    props: {
      fromPage:{
        type: String,
        default:""
      },
      selectdate: {
        type: Date,
        default: new Date(),
      },
      onchange: {
        type: Function,
        default: () => {
        }
      },
    },
    components: {},
    setup(props) {
      onMounted(
        () => {
        }
      )

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        show_picker: false,
        currentDate: new Date(),
        minDate: new Date(2020, 0, 1),
        maxDate: new Date(2025, 10, 1),
      })

      const left = () => {
        props.onchange(new Date(props.selectdate.setDate(props.selectdate.getDate() - 1)))
      }
      const right = () => {
        props.onchange(new Date(props.selectdate.setDate(props.selectdate.getDate() + 1)))
      }
      const onshow = () => {
        state.show_picker=true
      }
      const confirm = (data) => {
        props.onchange(data)
        state.show_picker=false
      }
      const cancel = () => {
        state.show_picker=false
      }

      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        left,
        right,
        moment,
        onshow,
        confirm,
        cancel,
      }
    }
  }
</script>
<style scoped lang="less">
    .div_changedate2_content {
        height: 64px;
        display: flex;
        background-color: #06B48D;
        padding: 0 10px 0 10px;
        align-items: center;
    }

    .div_changedate2_content_item {
        flex: 1;
        justify-content: center;
        align-items: center;
    }

    .svg_date {
        height: 44px;
        width: 18px;
        justify-content: center;
        align-items: center;
        fill: white;

    }

    .svg_arraw {
        height: 20px;
        width: 20px;
        justify-content: center;
        align-items: center;
        fill: white;
    }

    .span_changedate2_des {
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #FFFFFF;
    }

    .span_changedate2_date {
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #FFFFFF;
    }
</style>
