<!--三组圆形-->
<template>
    <div class="div_content_contianer" :style="{height: height}">
      <!--左侧圆圈-->
        <div class="div_circle_define" :style="{ height: sizeleft, width: sizeleft, borderColor: dataleft ? mColor: '#AAAAAA' }">
          <div class="div_center">
              <div class="div_layout_center" style="margin-bottom: 10px;" v-show="desleft">{{desleft}}</div>
              <div v-if="dataleft" class="span_small" :style="{ fontSize:fontsizeleft }">{{dataleft}}</div>
              <div v-else class="div_line_small"/>
          </div>
        </div>
      <!--中间圆圈-->
        <div class="div_circle_define" :style="{ height: sizecenter, width: sizecenter, borderColor: datacenter ? mColor: '#AAAAAA' }">
          <div class="div_center" >
              <div class="div_layout_center">{{descenter}}</div>
              <div v-if="datacenter" class="span_big" :style="{fontSize:fontsizecenter}">{{datacenter}}</div>
              <div v-else class="div_line_big"/>
              <div class="div_layout_center">{{unitcenter}}</div>
          </div>
       </div>
       <!--右侧圆圈-->
        <div class="div_circle_define" :style="{ height: sizeright, width: sizeright, borderColor: dataright ? mColor: '#AAAAAA' }">
          <div class="div_center">
              <div class="div_layout_center" style="margin-bottom: 10px;" v-show="desright">{{ desright }}</div>
              <div v-if="dataright" class="span_small" :style="{fontSize:fontsizeright}">{{dataright}}</div>
              <div  v-else class="div_line_small"/>
          </div>
        </div>
    </div>
</template>

<script>
  import { onMounted, onBeforeUnmount, reactive, toRefs,} from 'vue'

  export default {

    props: {
      mColor:{
        type: String,
        default: '#CE5204'
      },
      dataleft: { // 左侧数据
        type: Number,
        default: 0,
      },
      datacenter: { // 中间数据
        type: Number,
        default: 0,
      },
      dataright: { // 右侧数据
        type: Number,
        default: 0,
      },
      desleft: { // 左侧描述
        type: String,
        default: '',
      },
      descenter: { // 中间描述
        type: String,
        default: '',
      },
      unitcenter: { // 中间单位
        type: String,
        default: '',
      },
      desright: { // 右侧描述
        type: String,
        default: '',
      },

      sizeleft: { // 左侧大小
        type: String,
        default: '85px',
      },
      sizecenter: { // 中间大小
        type: String,
        default: '125px',
      },
      sizeright: { // 右侧大小
        type: String,
        default: '85px',
      },
      fontsizeleft: { // 左侧字体大小
        type: String,
        default: '12px',
      },
      fontsizecenter: { // 中间字体大小
        type: String,
        default: '24px',
      },
      fontsizeright: { // 右侧字体大小
        type: String,
        default: '12px',
      },
      height:{ // 组件高度
        type: String,
        default: '138px !important',
      },
    },
    setup(props) {

      onMounted(
        () => {
        }
      )
      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        currentRate: 1000
      })
      onBeforeUnmount(() => {
        // 清除旧组件的样式标签
        document.getElementById(state.idStr) && document.getElementById(props.idStr).remove()
      })
      return {
        ...toRefs(state)
      }
    },
  }
</script>
