<!--状态类型-->
<template>
    <div class="div_drinktype_content" :style="style">
        <img class="img_drinktype" :src="datas.length === 6?require('@/assets/group.png'):datas.length === 3?require('@/assets/group_three.png'):datas.length === 5?require('@/assets/track-sleep/group_five.png') :require('@/assets/progress.png')" alt=""/>
        <div class="div_drinktype_des" >
            <span :style="textstyle" v-for="(item,index) in datas" :key="index">{{item}}</span>
        </div>
        <div class="div_drinktype_arraw">
            <div v-for="item in (datas.length)" :key="item">
                <img v-show="type === item" class="img_drinktype_arraw" src="@/assets/arrow-up.svg" alt=""/>
                <div v-show="type !== item"/>
            </div>
        </div>
    </div>
</template>
<script>
  import {reactive, onMounted,toRefs} from 'vue'

  export default {
    name: "DrinkType",
    props: {
      type: {//代表箭头标签指向
        type: Number,
        default: 0
      },
      style: {
        type: String,
        default: ''
      },
      textstyle: {
        type: String,
        default: ''
      },
      datas: {
        type: [],
        default: ['非常不足','正常饮水','过量饮水','严重过量']
      },
    },
    components: {},
    setup() {
      onMounted(
        () => {
        }
      )

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        mData: [0,1,2,3]
      })

      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
      }
    }
  }
</script>
